import { Component, OnInit, Output, Inject, EventEmitter, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { PoRequisitionDetailMasterService, CodeCheckerService,ErrorHandleService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { ItemMasterService } from '../../../../shared';
import { DivisionMasterService,RequisitionDetailMasterService, CurrentUserService } from '../../../../shared';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-po-requisition-add-detail-master',
  templateUrl: './po-requisition-add-detail-master.component.html',
  styleUrls: ['./po-requisition-add-detail-master.component.scss']
})
export class PoRequisitionAddDetailMasterComponent implements OnInit {
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  @ViewChild('myComboBox') myComboBox: jqxComboBoxComponent;
  @ViewChild('reqItemsCombo') reqItemsCombo: jqxComboBoxComponent;
  @ViewChild('myStoreBox') myStoreBox: jqxComboBoxComponent;
  @ViewChild('myVendorBox') myVendorBox: jqxComboBoxComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('pComboBox') pComboBox: jqxComboBoxComponent;
  @ViewChild('purchasedate') purchasedate: jqxDateTimeInputComponent;
  @ViewChild('validdate') validdate: jqxDateTimeInputComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;

  apiUrl: string;
  porequisitionDetailForm: FormGroup;
  po_no: string;
  po_date: String;
  extra_ref: string;
  vendor: string;
  store: string;
  type: any;
  valid_up_to: string;
  typelist: Array<any> = [];
  division_data: any;
  divisionAdapter: any;
  store_data: any;
  storeAdapter: any;
  vendor_data: any;
  vendorAdapter: any;
  po_requisition_item: Array<any> = [];
  itemAdapter: any;
  reqSource:any ;
  reqAdapter:any;

  userData: any = {};

  storeItemChanged: boolean = false;
  vendorItemChanged: boolean = false;

  constructor(
    private poreqDetailService: PoRequisitionDetailMasterService,
    private itemService: ItemMasterService,
    private rds:RequisitionDetailMasterService,
    private fb: FormBuilder,
    private ehs: ErrorHandleService,
    private ccs: CodeCheckerService,
    @Inject('API_URL') apiUrl: string,
    private cdr: ChangeDetectorRef,
    private cus: CurrentUserService,
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData;
    this.porequisitionDetailForm = fb.group({
      'po_no': [null, Validators.required],
      'valid_up_to': [null, Validators.required],
      'po_date': [null, Validators.required],
      'extra_ref': [null],
    });
  }

  ngOnInit() {
    this.typelist = [
      {
        id: 1,
        name: 'Direct Purchase',
        value: 'dp'
      },
      {
        id: 2,
        name: 'Purchase using Indents',
        value: 'ip'
      }
    ]
    this.division_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        async: false,
        url: this.apiUrl + 'division?limit=all',
      };
    this.divisionAdapter = new jqx.dataAdapter(this.division_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'store?limit=all',
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
    this.vendor_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'vendor?limit=all',
      };
    this.vendorAdapter = new jqx.dataAdapter(this.vendor_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });

    this.reqSource =
    {
      datatype: "json",
      contentType: "application/json; charset=utf-8",
      datafields: [
        { name: 'id' },
        { name: 'indent_no'}
      ],
      data: this.userData,
      url: this.apiUrl + 'requisition_detail?limit=all'
    };
    this.reqAdapter = new jqx.dataAdapter(this.reqSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
  }

  ngAfterViewInit() {
    let codeData = {
      find_is_no:'po_requisition',
      return_type: 'for_client'
    }
    this.ccs.get(codeData).subscribe(
      response => {
        this.porequisitionDetailForm.controls['po_no'].setValue(response['po_no']);
      },
      error => {
        console.log(error);
      }
    );
    this.cdr.detectChanges();
  }

  getItemByRequisitionDetails(id) {
    if(id){
      this.rds.show(id).subscribe((res)=>{
        let item = res['data']['requisition_item_details'];
        let tData = [];
        for(let i=0; i<item.length; i++){
          let dt = {};
          dt['name'] = item[i]['item']['name'] + '-'+ item[i]['item']['code'];
          dt['id'] = item[i]['item']['id'];
          tData.push(dt);
        }
        this.itemAdapter = tData;
      })
    }
  }


  purchaseitems:any;
  reqItemSelect(){
    
    this.purchaseitems = [];
    this.source.localdata = [];
    this.purchaseitems = this.reqItemsCombo.getCheckedItems();
    // let rqitem = this.reqItemsCombo.getSelectedItem().value;
    for(let i = 0; i < this.purchaseitems.length; i++){
      let id = this.purchaseitems[i].value;
      this.jqxLoader.open();
      this.rds.show(id).subscribe(
        result => {  
            console.log(result);             
            let item = result['data']['requisition_item_details'];
            for (let i = 0; i < item.length; i++) {
              let dt = {};
              item[i]['requisition_id'] = result['data'].id;
              item[i]['item_name'] = item[i]['item']['name'] + '-' + item[i]['item']['code'];
              item[i]['unit_name'] = item[i]['item'] && item[i]['item']['unit'] && item[i]['item']['unit'].name || null;
              item[i]['unit_id'] = item[i]['item'] && item[i]['item']['unit_id'] || null;
              this.source.localdata.push(item[i]);
            }
            // this.source.localdata = item;
            // this.myGrid.updatebounddata();
            // if(i == this.purchaseitems.length - 1){
              this.myGrid.updatebounddata();
            // }
            this.jqxLoader.close();
        },
        error => {
          console.log(error);
          this.jqxLoader.close();
        }
      )
    }    
  }


  source: any =
    {
      localdata: this.po_requisition_item,
      datatype: 'json',
      datafields:
        [
          // { name: 'indent_no', type: 'string' },
          { name: 'requisition_id', type: 'string' },
          { name: 'item_id', type: 'string' },
          { name: 'item_name', type: 'string' },
          { name: 'quantity', type: 'number' },
          { name: 'rate', type: 'number' },
          // { name: 'vat_percent', type: 'number' },
          // { name: 'vat', type: 'number' },
          { name: 'total', type: 'number' },
          { name: 'remarks', type: 'string' },
        ],
      id: 'item_id',
      updaterow: (rowid: any, rowdata: any, commit: any): void => {
        commit(true);
      }
    };

  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] =
    [
      {
        text: 'REQ ID', datafield: 'requisition_id', displayfield: 'requisition_id' ,width: 70, editable:false, 
        // createeditor: (row: number, column: any, editor: any): void => {
        //   let contractorsource =
        //     {
        //       datatype: "json",
        //       contentType: "application/json; charset=utf-8",
        //       datafields: [
        //         { name: 'id' }
        //       ],
        //       url: this.apiUrl + 'requisition_detail?limit=all'
        //     };
        //   let contractorAdapter = new jqx.dataAdapter(contractorsource, {
        //     downloadComplete: (data, status, xhr) => {
        //       let tData = data.data;
        //       return tData;
        //     }
        //   });
        //   editor.jqxComboBox({
        //     source: contractorAdapter,
        //     displayMember: "id",
        //     valueMember: "id",
        //   });
        // },
        // cellsrenderer: (index: number, datafield: string, value: any, defaultvalue: any, column: any, rowdata: any): void => {
        //   this.getItemByRequisitionDetails(rowdata.requisition_id);
        // }
      },
      {
        text: 'Item code', datafield: 'item_id', displayfield: 'item_name', columntype: 'combobox', width: 120,
        createeditor: (row: number, column: any, editor: any): void => {
          editor.jqxComboBox({
            source: this.itemAdapter,
            displayMember: "name",
            valueMember: "id",
            searchMode: "containsignorecase"
          });
        }
      },
      { text: 'Quantity', columntype: 'textbox', datafield: 'quantity', width: 100,
        cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
          if (newvalue !== oldvalue) {
            let currentRow = this.myGrid.getrowdata(row);
            let rowId = this.myGrid.getrowdata(row).boundindex;
            currentRow['quantity'] = newvalue;
            this.amountCalculation(currentRow, rowId);
          }
        },
      },
      { text: 'Rate', datafield: 'rate', columntype: 'textbox', width: 70,
        cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
          if (newvalue !== oldvalue) {
            let currentRow = this.myGrid.getrowdata(row);
            let rowId = this.myGrid.getrowdata(row).boundindex;
            currentRow['rate'] = newvalue;
            this.amountCalculation(currentRow, rowId);
          }
        },
      },
      // { text: 'Vat %', datafield: 'vat_percent', columntype: 'textbox', width: 50,
      //   cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
      //     if (newvalue !== oldvalue) {
      //       let currentRow = this.myGrid.getrowdata(row);
      //       let rowId = this.myGrid.getrowdata(row).boundindex;
      //       currentRow['quantity'] = newvalue;
      //       this.amountCalculation(currentRow, rowId);
      //     }
      //   },
      // },
      // { text: 'Vat', datafield: 'vat', columntype: 'textbox', width: 120 },
      {
        text: 'Total', datafield: 'total', columntype: 'textbox', width: 120, editable: false 
        // cellsrenderer: (index: number, datafield: string, value: any, defaultvalue: any, column: any, rowdata: any): string => {
        //   if (rowdata.quantity && rowdata.rate && rowdata.vat) {
        //     let value = parseFloat(rowdata.quantity) * parseFloat(rowdata.rate);
        //     let total = value + ( value * (rowdata.vat/100));
        //     return '<div style="padding:5px;">' + this.dataAdapter.formatNumber(total, 'f2') + '</div>';
        //   }
        // }
      },
      { text: 'Remarks', datafield: 'remarks', columntype: 'textbox', width: 120 },
      {
        text: 'Actions', datafield: 'Delete', columntype: 'button',
        cellsrenderer: (): string => {
          return 'Delete';
        },
        buttonclick: (row: number): void => {
          let rowid = this.myGrid.getrowid(row)
          this.myGrid.deleterow(rowid);
        }
      }
    ];

  // rendertoolbar = (toolbar: any): void => {
  //   let container = document.createElement('div');
  //   container.style.margin = '5px';

  //   let buttonContainerAddRow = document.createElement('div');

  //   buttonContainerAddRow.id = 'buttonContainerAddRow';

  //   buttonContainerAddRow.style.cssText = 'float: right';

  //   container.appendChild(buttonContainerAddRow);
  //   toolbar[0].appendChild(container);

  //   let addRowButton = jqwidgets.createInstance('#buttonContainerAddRow', 'jqxButton', { width: 155, value: 'Add New Item' });


  //   addRowButton.addEventHandler('click', () => {
  //     let datarow = this.generaterow();
  //     this.myGrid.addrow(null, datarow);
  //   })

  // };

  amountCalculation(data, rowid){
    if(data){
      let total:number = 0;
      if(data['quantity'] && data['rate']){
        total = data['quantity'] * data['rate'];
      }
      this.myGrid.setcellvalue(rowid, 'total', total.toFixed(2));
    }
  }

  addNewItem(){
    let datarow = this.generaterow();
    this.myGrid.addrow(null, datarow);
  }

  generaterow(): any {
    let row = {};
    row['requisition_id'] = '';
    row['item_id'] = '';
    row['quantity'] = ''
    row['rate'] = '';
    // row['vat_percent'] = '13';
    // row['vat'] = '';
    row['total'] = '';
    row['remarks'] = '';
    return row;
  }

  saveBtn(post) {
    post['poItemRequisitionDetail'] = [];
    let gridRows = this.myGrid.getrows();
    post['poItemRequisitionDetail'] = gridRows;
    post['store_id'] = this.myStoreBox.val();
    post['vendor_id'] = this.myVendorBox.val();
    post['type'] =  'req'; //post['type'] = this.pComboBox.val();
    this.myValidator.validate(document.getElementById('porequisitionDetailForm'));
    if (post['store_id'] && post['vendor_id'] && post['type']) {
      this.jqxLoader.open();
      this.poreqDetailService.store(post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }
  }
  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      { input: '.po_no', message: 'Purchase required!', action: 'keydown, blur', rule: 'required' },
      {
        input: '.po_date', message: 'Purchase Date required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.purchasedate.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      // { input: '.extra_ref', message: 'Extra ref required!', action: 'keydown, blur', rule: 'required' },
      {
        input: '.store_id', message: 'Store required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.myStoreBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.vendor', message: 'Vendor required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.myVendorBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      // {
      //   input: '.purchasetype', message: 'Purchase Type required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
      //     let data = this.pComboBox.val();
      //     let result = (data != '') ? true : false;
      //     return result;
      //   }
      // },
      {
        input: '.valid_up_to', message: 'Valid Date required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.validdate.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
    ];

    storeChanged() {
      this.storeItemChanged = this.myStoreBox.val() ? true : false;
    }

    vendorChanged() {
      this.vendorItemChanged =  this.myVendorBox.val() == null ? false : true;
    }

}



