import { NgModule ,NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { PoRequisitionDetailMasterRoutingModule } from './po-requisition-master-routing.module';
import { PoRequisitionDetailMasterComponent } from './po-requisition-detail-master.component';
import { PoRequisitionEditDetailMasterComponent } from './po-requisition-edit-detail-master/po-requisition-edit-detail-master.component';
import { PoRequisitionAddDetailMasterComponent } from './po-requisition-add-detail-master/po-requisition-add-detail-master.component';
import { PoItemDetailsViewComponent } from './po-item-details-view/po-item-details-view.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    PoRequisitionDetailMasterRoutingModule
  ],
  entryComponents:[
    PoRequisitionEditDetailMasterComponent,   
    PoRequisitionAddDetailMasterComponent,
    PoItemDetailsViewComponent
  ],
  declarations: [
    PoRequisitionDetailMasterComponent,
    PoRequisitionEditDetailMasterComponent,   
   PoRequisitionAddDetailMasterComponent, PoItemDetailsViewComponent
    
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class PoRequisitionDetailModule { }



