import { Component, OnInit, Input, Inject, AfterViewInit, ChangeDetectorRef, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { PoRequisitionDetailMasterService, RequisitionDetailMasterService,ErrorHandleService, PoRequisition, CurrentUserService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-po-requisition-edit-detail-master',
  templateUrl: './po-requisition-edit-detail-master.component.html',
  styleUrls: ['./po-requisition-edit-detail-master.component.scss']
})
export class PoRequisitionEditDetailMasterComponent implements OnInit {
  @ViewChild('reqDetailEditGrid') reqDetailEditGrid: jqxGridComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('myComboBox') myComboBox: jqxComboBoxComponent;
  @ViewChild('myStoreBox') myStoreBox: jqxComboBoxComponent;
  @ViewChild('myVendorBox') myVendorBox: jqxComboBoxComponent;
  @ViewChild('pComboBox') pComboBox: jqxComboBoxComponent;
  @ViewChild('purchasedate') purchasedate: jqxDateTimeInputComponent;
  @ViewChild('validdate') validdate: jqxDateTimeInputComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Input('PoEditData') PoEditData;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;

  EditData: PoRequisition;
  porequisitionDetailForm: FormGroup;
  source: any;
  dataAdapter: any;
  columns: any[];
  apiUrl: string;
  ready: boolean = false;
  po_requisition_item: Array<any> = [];
  contractorsource: any;
  contractorAdapter: any;
  reqsource: any;
  reqAdapter: any;
  division_data: any;
  divisionAdapter: any;
  store_data: any;
  storeAdapter: any;
  vendor_data: any;
  vendorAdapter: any;
  itemAdapter: any;
  typelist: Array<any> = [];

  userData: any = {};

  constructor(private fb: FormBuilder,
    @Inject('API_URL') apiUrl: string,
    private cdr: ChangeDetectorRef,
    private rds: RequisitionDetailMasterService,
    private prdms: PoRequisitionDetailMasterService,
    private ehs: ErrorHandleService,
    private cus: CurrentUserService
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.createForm();
  }
  ngOnInit() {
    this.getItem();
    this.typelist = [
      {
        id: 1,
        name: 'Direct Purchase',
        value: 'dp'
      },
      {
        id: 2,
        name: 'Purchase using Indents',
        value: 'ip'
      }
    ]
    this.EditData = new PoRequisition(this.PoEditData);
    this.porequisitionDetailForm.setValue(this.EditData);
    this.prdms.show(this.PoEditData.id).subscribe(
      result => {
        this.po_requisition_item = result['data']['po_item_requisition_details'];
        for (var _i = 0; _i < this.po_requisition_item.length; _i++) {
          this.po_requisition_item[_i]['name'] = this.po_requisition_item[_i]['item'] && this.po_requisition_item[_i]['item'].code + '-' +this.po_requisition_item[_i]['item'].name;
        }
        this.source.localdata = this.po_requisition_item;
        this.reqDetailEditGrid.updatebounddata("cells");
      },
      error => {
        console.log(error);
      });

    this.division_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        async: false,
        url: this.apiUrl + 'division?limit=all',
      };
    this.divisionAdapter = new jqx.dataAdapter(this.division_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        url: this.apiUrl + 'store?limit=all',
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        this.myStoreBox.selectItem(this.PoEditData.store_id);
      }
    });
    this.vendor_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'vendor?limit=all',
      };
    this.vendorAdapter = new jqx.dataAdapter(this.vendor_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        this.myVendorBox.selectItem(this.PoEditData.vendor_id);
      }
    });
  }


  createForm() {
    this.porequisitionDetailForm = this.fb.group({
      'po_no': [null, Validators.required],
      'po_date': [null, Validators.required],
      'extra_ref': [null, Validators.required],
      'valid_up_to': [null, Validators.required],
    });
  }

  ngAfterViewInit() {
    // if(this.PoEditData.type){
    //   this.pComboBox.selectItem(this.PoEditData.type);
    // }
    if(this.PoEditData.division_id){
      this.myComboBox.selectItem(this.PoEditData.division_id);
    }
    
    this.cdr.detectChanges();
  }

  getItemByRequisitionDetails(id) {
    if (id) {
      this.rds.show(id).subscribe((res) => {
        let item = res['data']['requisition_item_details'];
        let tData = [];
        for (let i = 0; i < item.length; i++) {
          let dt = {};
          dt['name'] = item[i]['item']['name'] + '-' + item[i]['item']['code'];
          dt['id'] = item[i]['item']['id'];
          tData.push(dt);
        }
        this.contractorAdapter = tData;
      })
    }
  }

  getItem() {
    this.source =
      {
        localdata: {},
        datatype: 'json',
        datafields:
          [
            { name: 'requisition_id', type: 'string' },
            { name: 'item_id', type: 'string' },
            { name: 'name', type: 'string' },
            { name: 'quantity', type: 'number' },
            { name: 'rate', type: 'number' },
            { name: 'vat', type: 'number' },
            { name: 'total', type: 'number' },
            { name: 'remarks', type: 'string' },
          ],
        id: 'item_id',
        updaterow: (rowid: any, rowdata: any, commit: any): void => {
          commit(true);
        }
      };

    this.dataAdapter = new jqx.dataAdapter(this.source);
    
    // this.reqsource =
    //   {
    //     datatype: "json",
    //     contentType: "application/json; charset=utf-8",
    //     datafields: [

    //       { name: 'requisition_id', type: 'string' }
    //     ],
    //     url: this.apiUrl + 'requisition_detail?limit=all'
    //   };
    // this.reqAdapter = new jqx.dataAdapter(this.reqsource, {
    //   downloadComplete: (data, status, xhr) => {
    //     let tData = data.data;
    //     for (var _i = 0; _i < tData.length; _i++) {
    //       tData[_i]['requisition_id'] = tData[_i]['id'];
    //       // tData[_i]['name'] = tData[_i]['item'] && tData[_i]['item']['name'] + ' - ' + tData[_i]['item']['code'];
    //     }
    //     return tData;
    //   }
    // });

    this.columns =
      [
        {
          text: 'REQ ID', datafield: 'requisition_id', editable:false, width:70,
          // columntype: 'combobox',
          // createeditor: (row: number, cellvalue: string, editor: any, celltext: string, cellwidth: number, cellheight: number): void => {
          //   editor.jqxComboBox({
          //     source: this.reqAdapter,
          //     displayMember: "requisition_id",
          //     valueMember: "requisition_id"
          //   });
          // },
          // cellsrenderer: (index: number, datafield: string, value: any, defaultvalue: any, column: any, rowdata: any): void => {
          //   this.getItemByRequisitionDetails(rowdata.requisition_id);
          // }
        },
        {
          text: 'Item Code', datafield: 'item_id', displayfield: 'name', editable:false
          //  columntype: 'combobox',
          // initeditor: (row: number, cellvalue: string, editor: any, celltext: string, cellwidth: number, cellheight: number): void => {
          //   editor.jqxComboBox({
          //     source: this.contractorAdapter,
          //     displayMember: "name",
          //     valueMember: "id"
          //   });
          // }
        },
        { text: 'Quantity', columntype: 'textbox', datafield: 'quantity', width: 120,
          cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
            if (newvalue !== oldvalue) {
              let currentRow = this.reqDetailEditGrid.getrowdata(row);
              let rowId = this.reqDetailEditGrid.getrowdata(row).boundindex;
              currentRow['quantity'] = newvalue;
              this.amountCalculation(currentRow, rowId);
            }
          },
        },
        { text: 'Rate', datafield: 'rate', columntype: 'textbox', width: 70,
          cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
            if (newvalue !== oldvalue) {
              let currentRow = this.reqDetailEditGrid.getrowdata(row);
              let rowId = this.reqDetailEditGrid.getrowdata(row).boundindex;
              currentRow['rate'] = newvalue;
              this.amountCalculation(currentRow, rowId);
            }
          },
        },
        // { text: 'Vat', datafield: 'vat', columntype: 'textbox', width: 120 },
        {
          text: 'Total', datafield: 'total', editable: false, width: 120,
          cellsrenderer: (index: number, datafield: string, value: any, defaultvalue: any, column: any, rowdata: any): string => {
            if (rowdata.quantity && rowdata.rate && rowdata.vat) {
              let value = parseFloat(rowdata.quantity) * parseFloat(rowdata.rate);
              let total = value + ( value * (rowdata.vat/100));
              return '<div style="padding:5px;">' + this.dataAdapter.formatNumber(total, 'f2') + '</div>';
            }
          }
        },
        { text: 'Remarks', datafield: 'remarks', columntype: 'textbox', width: 120 },
        {
          text: 'Delete', datafield: 'Delete', columntype: 'button', width: 70,
          cellsrenderer: (): string => {
            return 'Delete';
          },
          buttonclick: (row: number): void => {

            let rowID = this.reqDetailEditGrid.getrowid(row);
            this.reqDetailEditGrid.deleterow(rowID);
          }
        }
      ];

  }

  amountCalculation(data, rowid){
    if(data){
      let total:number = 0;
      if(data['quantity'] && data['rate']){
        total = data['quantity'] * data['rate'];
      }
      this.reqDetailEditGrid.setcellvalue(rowid, 'total', total.toFixed(2));
    }
  }

  rendertoolbar = (toolbar: any): void => {
    console.log('Add toolbar rendered');
    let container = document.createElement('div');
    container.style.margin = '5px';
    let buttonContainerPoTitle = document.createElement('div');
    // let buttonContainerPoEditAddRow = document.createElement('div');

    buttonContainerPoTitle.id = 'buttonContainerPoTitle';
    // buttonContainerPoEditAddRow.id = 'buttonContainerPoEditAddRow';

    buttonContainerPoTitle.style.cssText = 'float: left;padding:5px;margin-right:10px;font-weight:bold;';
    // buttonContainerPoEditAddRow.style.cssText = 'float: right';

    buttonContainerPoTitle.innerHTML = 'Requisition Details:';

    container.appendChild(buttonContainerPoTitle);
    // container.appendChild(buttonContainerPoEditAddRow);
    toolbar[0].appendChild(container);

    // let addRowButton = jqwidgets.createInstance('#buttonContainerPoEditAddRow', 'jqxButton', { width: 155, value: 'Add New Item' });


    // addRowButton.addEventHandler('click', () => {
    //   let datarow = this.generaterow();
    //   this.reqDetailEditGrid.addrow(null, datarow);
    // })

  };

  generaterow(): any {
    let row = {};
    row['requisition_id'] = '';
    row['item_id'] = '';
    row['quantity'] = ''
    row['rate'] = '';
    row['vat'] = '';
    row['total'] = '';
    row['remarks'] = '';
    return row;
  }
  saveBtn(post) {
    post['poItemRequisitionDetail'] = [];
    let gridRows = this.reqDetailEditGrid.getrows();
    post['poItemRequisitionDetail'] = gridRows;
    post['store_id'] = this.myStoreBox.val();
    post['vendor_id'] = this.myVendorBox.val();
    post['type'] =  'req'; //post['type'] = this.pComboBox.val();
    this.myValidator.validate(document.getElementById('porequisitionDetailForm'));
    if (post['store_id'] && post['vendor_id'] && post['type']) {
      this.jqxLoader.open();
      this.prdms.update(this.PoEditData.id, post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.UpdatedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();

        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        });
    }
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      { input: '.po_no', message: 'Purchase required!', action: 'keydown, blur', rule: 'required' },
      {
        input: '.po_date', message: 'Purchase Date required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.purchasedate.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      { input: '.extra_ref', message: 'Extra ref required!', action: 'keydown, blur', rule: 'required' },
      {
        input: '.store_id', message: 'Store required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.myStoreBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.vendor', message: 'Vendor required!', action: 'keydown,blur,change', rule: (input: any, commit: any): any => {
          let data = this.myVendorBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      // {
      //   input: '.purchasetype', message: 'Purchase Type required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
      //     let data = this.pComboBox.val();
      //     let result = (data != '') ? true : false;
      //     return result;
      //   }
      // },
      {
        input: '.valid_up_to', message: 'Valid Date required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.validdate.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
    ];
}



