import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PoRequisitionDetailMasterComponent } from './po-requisition-detail-master.component';

const routes: Routes = [
    {
        path: '', component: PoRequisitionDetailMasterComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PoRequisitionDetailMasterRoutingModule { }


